//
export function getLogin(phoneNumber, password) {
    var data = null;
    userList.forEach(item => {
        if (item.phoneNumber === phoneNumber) {
            if (item.password === password) {
                item.token = (Math.random().toString() + "00000").slice(2, 7) + "DFImjOID57KHGhyw_47JH"
                data = { code: 0, msg: '', data: item };
                return;
            } else {
                data = { code: 2, msg: '' };
                return;
            }
        }
    })
    if (!data) {
        data = { code: 1, msg: '' };
    }
    return data;
}
//
export function getUserInfo(id) {
    var data = null;
    userList.forEach(item => {
        if (item.uid === id) {
            data = item
        }
    })
    return data;
}

//
export function getArticleLists(classifyId) {
    let list = [];
    if (classifyId === '') {
        list = articleList;
    } else {
        articleList.forEach(item => {
            if (item.classifyId === classifyId) {
                list.push(item);
            }
        })
    }
    return list;
}
//
export function getArticle(id) {
    id = Number(id);
    if (id != NaN) {
        let article = {};
        articleList.forEach(item => {
            if (item.id === id) {
                article = item;
            }
        })
        return article;
    }
}
//
export function getComment(id) {
    id = Number(id);
    if (id != NaN) {
        let comment = [];
        commentList.forEach(item => {
            if (item.article_id === id) {
                comment.push(item);
            }
        })
        return comment;
    }
}

//
export function submitComment(data) {
    let newData = {}
    newData.id = commentList.length + 1
    newData.article_id = Number(data.article_id)
    newData.comment_content = data.content
    newData.user_id = Number(data.user_id)
    newData.re_username = data.re_username
    newData.re_comment_id = data.re_comment_id
    newData.created_time = Math.floor(new Date().getTime() / 1000)
    newData.reply_num = 0
    newData.like_number = 0
    commentList.push(newData)//
    //id
    commentList.forEach(item => {
        if (item.id === data.re_comment_id) {
            item.reply_num++
        }
        if (item.id === data.subCommentId) {
            item.reply_num++
        }
    })
    //
    return { err: 0, msg: '' }
}

//
export function clickZanReq(uid, comment_id) {
    commentList.forEach(item => {
        if (item.id === Number(comment_id)) {
            item.like_number++
            userList.forEach(x => {
                if (x.uid === Number(uid)) {
                    x.like_comment.push(Number(comment_id))
                }
            })
        }
    })
    return { err: 0, msg: '' }
}

//
export function reqClickZanCommentId(uid) {
    let data = []
    if(uid){
        userList.forEach(item => {
            if (item.uid === Number(uid)) {
                data = item.like_comment
            }
        })
    }
    return { err: 0, msg: '', data: data }
}
//
export function cancleClickZanReq(uid, comment_id) {
    commentList.forEach(item => {
        if (item.id === Number(comment_id)) {
            item.like_number--
            userList.forEach(item => {
                if (item.uid === Number(uid)) {
                    let x = item.like_comment.indexOf(Number(comment_id))
                    if (x != -1) {
                        item.like_comment.splice(x, 1)
                    }
                }
            })
        }
    })

    return { err: 0, msg: '' }
}

//
export function chongDian(uid,article_id){
    let data = []
    if(uid){
        userList.forEach(item=>{
            if(item.uid === uid){
                if(item.like_article.includes(article_id)){
                    let x = item.like_article.indexOf(Number(article_id))
                    item.like_article.splice(x, 1)
                    //--
                    articleList.forEach(item=>{
                        if(item.id === article_id){
                            item.like_number--
                        }
                    })
                }else{
                    item.like_article.push(article_id)
                    articleList.forEach(item=>{
                        if(item.id === article_id){
                            item.like_number++
                        }
                    })
                }
                data.push(...item.like_article)
            }
        })
    }
    
    return {err:0,msg:'',data:data}
}
//
export function reqUserLikeArticle(uid){
    let data = []
    if(uid){
        userList.forEach(item => {
            if (item.uid === Number(uid)) {
                data = item.like_article
            }
        })
    }
    return { err: 0, msg: '', data: data } 
}



